.portfolio-component{
  border: 1px dashed #ffffff;
  border-radius: 3px;
  padding: 1rem 1.5rem;
}


.home-card > div {
  border: 1px dashed #ffffff;
  transition: 1s;
}

.home-card > div:hover {
  border: 1px solid #ffffff;
  position: relative;
  
  animation: homecardscalein 0.3s both linear;
  
}
.home-card-index > div > * {
  z-index: 10000;
}

@keyframes homecardscalein {
  from {
    scale: 1;
    opacity: 0.7;

  }
  to {
    scale: 1.1;
    opacity:1;
  }
}


/* input */
.portfoliotextbox{
  background-color: #ffffff00;
  text-align: center;
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  line-height: 2rem;
}

.portfoliotextbox:active, .portfoliotextbox:focus, .portfoliotextbox:focus-visible {
  background-color: #ffffff00;
  border: 0px solid #FFFFFF00;
  outline: none;
}