.settings {
  /* opacity: 0; */
  top: -100%;
  left: 50%;
  z-index: 1000;
  transform: translate(-50%, -50%);
  min-width: 80%;
  font-family: "Roboto", sans-serif;
  transition: 1s;
  font-weight: 200;
}

.show-settings {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 1s;
}

.settings-item {
  position: relative;
  left: 0px;
  opacity: 1;
  transition: 0.2s;
  max-width: 90%;
}
.settings-item-hide {
  left: -1070px;
  opacity: 0;
  transition: 1s;
}

.custom-settings {
  min-height: 60vh;
  max-height: 60vh;
  
  display: flex;
  justify-items: center;
  flex-direction: column;
  align-items: center;
}
.home-title-content {
  overflow-x: auto;
  overflow-y: auto;
}

@media only screen and (max-width: 640px) {
  body {
    background-color: lightblue;
  }
}

.setting-sidebar {
  background: url("../../assets/patterns/settings-sidebar.svg");
  border: 2px solid white;
}
.setting-sidebar-button.is-active{
  background-color: #f0f8ff0c;

}
.setting-sidebar-button:hover {
  background-color: #f0f8ff0c;
}

.settings-close-btn {
  color: White;
  transform: rotate(0deg);
  transition: 1s;
}

.settings-close-btn:hover {
  color: #f44336;
  transform: rotate(180deg);

  transition: 1s;
}

@keyframes rotateOn {
  to {
    transform: rotate(360deg);
  }
  from {
    transform: rotate(0deg);
  }
}
