.card-component {
  /* border: 1px dashed #ffffff; */
  border-radius: 3px;
  padding: 1rem 1.5rem;
  color: #ffffff;
  overflow-y: hidden;
}

.member-hide {
  position: relative;
  animation: hidemembercard 1s both ease-out;
}

.member-show {
  position: static;
  animation: showmembercard 1s both ease-in;
}

/* Animation css */
@keyframes hidemembercard {
  0% {
    opacity: 1;
    margin-top: 0px;
    height: 100%;
    top: 0px;
  }
  40% {
    opacity: 0.4;
    margin-top: 100px;
    height: 0px;
    top: 50px;
  }
  100% {
    opacity: 0;
    margin-top: 100px;
    height: 0px;
    top: 100px;
  }
}

@keyframes showmembercard {
  0% {
    opacity: 0;
    margin-top: 100px;
    height: 0px;
    top: 100px;
  }
  40% {
    opacity: 0.4;
    margin-top: 100px;
    height: 40%;
    top: 20px;
  }
  100% {
    opacity: 1;
    margin-top: 0px;
    height: 100%;
    top: 0px;
  }
}
