.skill-component {
  border: 1px dashed #ffffff;
  border-radius: 3px;
  padding: 1rem 1.5rem;
  backdrop-filter: blur(20px);
}

.skill-card {
  border: 1px dashed #ffffff;
  padding: 20px!important;
}

.skill-card:hover {
  border: 1px solid #ffffff;
  animation: skillcardscalein 0.3s both linear;
  z-index: 10000;
  
}

@keyframes skillcardscalein {
  from {
    scale: 1;
    opacity: 0.7;
  }
  to {
    scale: 1.2;
    opacity: 1;
  }
}

/* Star Component */

.stars {
  display: flex;
}

.star {
  padding: 0.2rem;
}
.star:hover {
  animation: rotation 0.1s infinite linear;
}
.star-complete {
  color: #97dece;
}

.star-incomplete {
  color: #fffbeb;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

/* Skill type css */
