.background {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -10000;
}

.background #grid {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(50px, 1fr));
  justify-content: center;
  --borderWidth: 3px;
  position: relative;
  border-radius: var(--borderWidth);
}

.background #grid:after {
  content: "";
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth));
  width: calc(100% + var(--borderWidth) * 2);
  background: rgba(255, 0, 89, 1);
  background: linear-gradient(
    45deg,
    #f79533,
    #f37055,
    #ef4e7b,
    #a166ab,
    #5073b8,
    #1098ad,
    #07b39b,
    #6fba82
  );
  z-index: -10000;
  animation: gradient 15s ease infinite;
  background-size: 400% 400%;
}

.background #grid .grid-item {
  /* margin: 0.5px; */
  cursor: pointer;
  position: relative;
  background-color: #171717;
}

.background #grid .grid-item:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: block;
}

.background #grid .grid-item:hover {
  opacity: 1;
  border: 1px solid #ffffff;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.grid-border {
  background-color: #b01e28 !important;
  border: 1px solid #b01e28;
  -webkit-mask-image: url("../../assets/background/hovericon.svg");
  -webkit-mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-image: url("../../assets/background/hovericon.svg");
  mask-size: cover;
  mask-position: center;
  mask-repeat: no-repeat;
  animation: heartmovement 1s infinite linear, gridopacity 0.5s linear;
}

.grid-border::after {
  padding: 10px;
}

@keyframes gridopacity {
  0% {
    opacity: 0;
    mask-size: 1.5em 1.5em;
  }
  100% {
    opacity: 1;
    mask-size: 2em 2em;
  }
}

@keyframes heartmovement {
  0% {
    mask-size: 1.5em 1.5em;
  }
  50% {
    mask-size: 2em 2em;
  }
  100% {
    mask-size: 1.5em 1.5em;
  }
}

.wallpaper {
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 1;
  transition: 1s opacity;
  background-position: center;
}
 
.wallpaper > .wallpaper-stars{
  background: url("../../assets/background/background.svg");

}