.control-buttons {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.control-buttons .next {
  width: 6rem;
}

.control-buttons .prev {
  width: 6rem;
}

