.button {
  display: inline-block;
  padding: 0.9rem 1.8rem;
  font-size: 16px;
  font-weight: 700;
  color: white;
  cursor: pointer;
  position: relative;
  background-color: transparent;
  text-decoration: none;
  overflow: hidden;
  z-index: 1;
  font-family: inherit;
  border-radius: 3px;
  min-width: 2rem;
  border: 1px solid #fff8e4;
  transition: 1s;
  perspective: 240px;
}

 
.button:hover .nextcomp {
  color: #fff8e4;
  rotate: 1 1 0 360deg;
  transition: 1s;
}

.prevcomp {
  animation: in 1s;
}

.button:hover {
  background-color: #EB455F;
  transition: 1s;
}

.button:hover .prevcomp {
  color: #fff8e4;
  rotate: 1 1 0 360deg;
  transition: 1s;
}
 
.inside-section {
  transition: opacity 1s;
}

.active {
  opacity: 0;
  transition: opacity 1s;
}

.about-loading {
  text-align: center;
  opacity: 0;
  transition: opacity 1s;
  position: relative;
  top: 30vh;
  display: flex;
  justify-content: center;
}
.about-loading-active {
  text-align: center;
  opacity: 1;
  transition: opacity 1s;
}


/* Text Animations */ 
.title-text {
  position: relative;
}

.title-text .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
  line-height: 1em;
}

.title-text .line {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  transform-origin: 0.5 0;
}

.title-text .ampersand {
  width: 1em;
  margin-right: -0.1em;
  margin-left: -0.1em;
}

.title-text .letters {
  display: inline-block;
  opacity: 0;
}