.nav-background {
  background: url("../../../assets/background/nav-background.svg");
}

.link-underline {
  border-bottom-width: 0;
  border-radius: 3px;
  font-size: 1rem;
  position: relative;
  transition: 2s ease;
}

.link-underline:hover span {
  animation: flip 2s infinite;
}

.link-underline-active span {
  animation: flip 5s infinite;
}

@keyframes flip {
  0%{
    opacity: 0;
  }
  80% {
    transform: rotateY(360deg);
    opacity: 0.6;
  }
}

.nav-link-active {
  background-size: 100% 100%;
  transition: background-size 0.2s ease-out;
  border-radius: 6px;
  min-width: 200px;
  background: #ffffff;
  color: rgba(0, 0, 0, 0);
  height: 3.5rem;
  z-index: 10000;
  position: absolute;
  top: 0px;

  margin-left: auto;
  margin-right: 100px;
  left: 0;
  right: 0;
  text-align: center;

  width: 100vw;
  opacity: 1;
  transition: 2s;
  margin-top: 10px;
  transition: 1s;
}

.clicktouplabel {
  width: 0px;
  height: 0px;
}

#clicktoup {
  background-color: #59c1bd;
  display: none;
  position: fixed;
  left: 0;
  z-index: 20000;
}

#clicktoup:checked ~ .nav-link-active {
  top: -300px;
  transition: 2s;
}

.nav-link-active > * {
  /* font-size: 5rem; */
  font-family: Kanit-SB;
  /* background-color: white; */
  transition: all 0.7s ease-out;
}

.nav-link-image-1.nav-link-active > span > span:nth-child(2) {
  color: #59c1bd !important;
}

.nav-link-image-2.nav-link-active > span > span:nth-child(2) {
  color: #a350cc !important;
}

.nav-link-image-3.nav-link-active > span > span:nth-child(2) {
  color: #fc994e !important;
}

.nav-link-image-4.nav-link-active > span > span:nth-child(2) {
  color: #eb455f !important;
}

/* text animation css */

.link-text {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.link-text span:nth-child(1) {
  position: absolute;
}

.link-text span:nth-child(2) {
  animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
  0% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}

/* Animating Show Navbar - Mobile */
.mobile-menu {
  position: fixed;
  z-index: 100000;
  /* color: #171717; */
  background-image: url("../../../assets/patterns/navbar-mobile.svg");
  top: -800px !important;
  width: 98vw;
  min-height: 60vh;
  transition: 1s;
  display: flex;
  justify-items: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 40px;
  border-radius: 10px;
  margin: 1vw;
  border: 2px solid white;
}

.mobile-menu-animate {
  top: 100px !important;
  transition: 0.6s;
}

.rotate-close {
  color: White;
  transform: rotate(0deg);
  transition: 1s;
}
.rotate-close:hover {
  color: #f44336;
  transform: rotate(180deg);
  transition: 1s;
}

@keyframes rotateOn {
  to {
    transform: rotate(360deg);
  }
  from {
    transform: rotate(0deg);
  }
}

/* Menu CSS */
.custom-menu {
  font-family: "Roboto", sans-serif;
  text-align: center;
}

.custom-menu > * {
  transition: 1s;
}

.custom-menu > button:nth-child(1):hover {
  background-color: #59c1bd;
  color: white;
  transition: 0.2s;
}
.custom-menu > button:nth-child(3):hover {
  background-color: #fc994e;
  color: white;
  transition: 0.2s;
}
.custom-menu > button:nth-child(5):hover {
  background-color: #eb455f;
  color: white;
  transition: 0.2s;
}
