.carousel {
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  padding: 0.2rem !important;
  /* backdrop-filter: blur(2px); */
  /* min-height: 50vh;
  min-width: 100vw; */
  border-radius: 6px;
}

.carousel-text {
  position: relative;
  top: 5em;
  /* overflow-y: auto; */
}

.carousel-text > p {
  font-size: 3rem;
  color: #ffffff;
  text-align: center;
  font-weight: 900;
  width: 100%;
  font-family: "Asterone-Regular";
}

.carousel-text-span {
  /* position: relative;
  top:20vh; */
}

.carousel-text-span .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.2em;
  padding-right: 0.05em;
  padding-bottom: 0.1em;
  overflow: hidden;
}

.carousel-text-span .letter {
  display: inline-block;
  line-height: 1em;
}

.carousel-image {
  position: fixed;
  top: 10em;

  z-index: -100;
}
.carousel-image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  translate: scale(20em);
}

.to-left-mask {
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.8) 10%,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 1) 75%,
    rgba(0, 0, 0, 0.8) 90%,
    rgba(0, 0, 0, 0) 100%
  );
  mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.8) 10%,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 1) 75%,
    rgba(0, 0, 0, 0.8) 90%,
    rgba(0, 0, 0, 0) 100%
  );
}

.to-bottom-mask {
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.7) 10%,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 1) 75%,
    rgba(0, 0, 0, 0.7) 90%,
    rgba(0, 0, 0, 0) 100%
  );
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.7) 10%,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 1) 75%,
    rgba(0, 0, 0, 0.7) 90%,
    rgba(0, 0, 0, 0) 100%
  );
}

