@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&family=Roboto:wght@300;400&display=swap");
@layer base {
  html {
    font-family: "Poppins", sans-serif;
  }

  @font-face {
    font-family: AsteroneOutline-Regular;
    src: url(./assets/fonts/Asterone/AsteroneOutline-Regular.otf);
  }
  @font-face {
    font-family: AsteroneOutline-Bold;
    src: url(./assets/fonts/Asterone/AsteroneOutline-Bold.otf);
  }

  @font-face {
    font-family: Asterone-Regular;
    src: url(./assets/fonts/Asterone/Asterone-Regular.otf);
  }
  @font-face {
    font-family: Asterone-Bold;
    src: url(./assets/fonts/Asterone/Asterone-Bold.otf);
  }
  @font-face {
    font-family: Gabriola;
    src: url(./assets/fonts/Gabriola.ttf);
  }
  @font-face {
    font-family: Inter;
    src: url(./assets/fonts/Inter.ttf);
  }
  @font-face {
    font-family: CairoPlay;
    src: url(./assets/fonts/CairoPlay.ttf);
  }
  @font-face {
    font-family: Satisfy;
    src: url(./assets/fonts/Satisfy.ttf);
  }

  @font-face {
    font-family: Cinzel;
    src: url(./assets/fonts/Cinzel.ttf);
  }

  @font-face {

    font-family: Kanit-Blk;
    src: url(./assets/fonts/Kanit/Kanit-Black.ttf);
  }

  @font-face {
    font-family: Kanit-SB;
    src: url(./assets/fonts/Kanit/Kanit-SemiBold.ttf);
  }

  @font-face {
    font-family: Kanit-EB;
    src: url(./assets/fonts/Kanit/Kanit-ExtraBold.ttf);
  }

  @font-face {
    font-family: Kanit-B;
    src: url(./assets/fonts/Kanit/Kanit-Bold.ttf);
  }

  @font-face {
    font-family: Kanit-L;
    src: url(./assets/fonts/Kanit/Kanit-Light.ttf);
  }
  @font-face {
    font-family: Kanit-EL;
    src: url(./assets/fonts/Kanit/Kanit-ExtraLight.ttf);
  }

}

.edit-cursor:hover {
  cursor: url("./assets/cursor/box-hover.svg") 128 128, pointer;
}

.edit-cursor {
  cursor: url("./assets/cursor/box-non-edit-hover.svg") 128 128, pointer;
}

.non-edit-cursor {
  cursor: url("./assets/cursor/box-non-edit-hover.svg") 128 128, pointer;
}

.font-title {
  font-family: Cinzel;
}

.font-breadcrumb {
  font-family: Gabriola;
}

.font-portfolio {
  font-family: Satisfy;
}

.font-skills {
  font-family: Gabriola;
}

.font-teams {
  font-family: Gabriola;
}

.font-navbar {
  font-family: Kanit-EL;
  font-size: 1.5em;
  font-weight: 100;
}

.font-weather {
  font-family: Gabriola;
}

.font-Asterone {
  font-family: Asterone-Regular;
}
.font-Asterone-B {
  font-family: Asterone-Bold;
}

.font-Asterone-O {
  font-family: AsteroneOutline-Regular;
}
.font-Asterone-OB {
  font-family: AsteroneOutline-Bold;
}

.custom-toast {
  font-family: CairoPlay;
  font-size: 12px;
}

/* Scroll bar CSS */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
